	import ctUploader from '@/components/ct-uploader.vue'
export default {
	components: {
		ctUploader
	},
  data() {
    return {
      activeBtn: '2',
      troubles: [
        {
          id: this.$dayjs().unix(),
          grade: '1',
          content: '',
          fileList: [
            { url: 'https://img.yzcdn.cn/vant/cat.jpeg' },
            // Uploader 根据文件后缀来判断是否为图片文件
            // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
            // { url: 'https://cloud-image', isImage: true }
          ]
        }
      ],
      isUploading: false,
      checkValue: '前往查看',
      showPicker: false,
      columns: ['前往查看', '人员上报'],
    }
  },
	onLoad() {
		// 动态设置页面标题
		uni.setNavigationBarTitle({
			title: '纳米氧化物光电实验室'
		})
	},
  methods: {
    /**
     * 处理按钮激活状态
     * @param index {String} 按钮序列
     */
    handleChangeBtn(index) {
      this.activeBtn = index
    },
    /**
     * 修改隐患等级
     * @params trouble {Object} 需要修改的对象
     * @params value {String} 修改的值
     */
    handleGradeChange(trouble, value) {
      this.$set(trouble, 'grade', value)
    },
    /**
     * 文件上传的方法
     */
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      console.log(file)
    },
    /**
     * 删除文件的方法
     */
    handleDelImg(res) {
      console.log('删除文件的方法', res)
    },
    /**
     * 添加隐患
     */
    handleAddTrouble() {
      this.troubles.push({
        id: this.$dayjs().unix(),
        grade: '1',
        content: '',
        fileList: [
          { url: 'https://img.yzcdn.cn/vant/cat.jpeg' },
          // Uploader 根据文件后缀来判断是否为图片文件
          // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
          // { url: 'https://cloud-image', isImage: true }
        ]
      })
    },
    /**
     * 删除隐患条目
     * @param trouble {Object}
     */
    handleDelTrouble(trouble) {
      const index = this.troubles.findIndex(item => item.id === trouble.id)
      console.log(index)
      if (index > -1) {
        this.troubles.splice(index, 1)
      }
    },
    /**
     * 上报隐患
     */
    handleUpload() {
      this.isUploading = true
      setTimeout(() => {
        this.$toast('隐患上报成功')
        this.isUploading = false
      }, 3000)
    },
    /**
     * picker选定事件
     */
    onConfirm(value) {
			console.log("value: " + JSON.stringify(value));



			

			this.checkValue = value.detail.value

			
      this.showPicker = false
    },
  }
}
